//<ГОТОВЫЕ КЛАССЫ>====================================================================================================
.d-flex {
	display: flex;
	// align-items: center;
}

//====================================================================================================
// Стили фоновой картинки
._bgi {
	background-position: 0 0;
	background-size: cover;
	background-repeat: no-repeat;
}

//====================================================================================================
// Затемняет остальные пункты меню при наведении на основной
.menu__link {
	// padding: 0 16px;
	transition: opacity 0.3s;
}

.sibling-fade:hover .menu__link:not(:hover) {
	opacity: 0.5;
}

//====================================================================================================
// Затемнение блока при наведении
.nuar_blk {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	width: 100%;
	height: 100%;
	background-color: #00000035;

	&:hover {
		background-color: #00000050;
	}
}

//====================================================================================================
// Наложение труегольного блока
.nuar_color {
	position: relative;
	width: 300px;
	height: 160px;
	border-radius: 25px;
	overflow: hidden;
	filter: drop-shadow(0px 0px 6px rgba(0, 0, 0, 0.5));

	&:before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background-image: radial-gradient(circle at 100% 0%, #fec38e, #ff7700 70%);
		border-radius: inherit;
		transform: skewY(-12deg);
		transform-origin: 100% 50%;
	}
}

//====================================================================================================
.row:after {
	display: block;
	content: "";
	clear: both;
}

.rub:after {
	content: "₽";
}

//====================================================================================================
// Мигающий виджет
.callback-widget {
	position: fixed;
	bottom: 3%;
	right: 1%;
	z-index: 500;
	content: '';
	width: 70px;
	height: 70px;
	background-size: 100%;
	background-position: 0 0;
	background-repeat: no-repeat;
	background-image: url("data:image/svg+xml;base64,");
}

.callback-widget-mob {
	display: none;
}

.blink {
	animation: blink 3s infinite;
	-webkit-animation: blink 3s linear infinite;
}

@keyframes blink {
	50% {
		opacity: 0.7;
	}
}

// <a href="#callback" class="callback-widget blink _popup-link"></a>
// <a href="tel:79533552332" class="callback-widget callback-widget-mob blink"></a>
//====================================================================================================
// Параллакс картинка
.parallax {
	background: url('../img/smart-conven.jpg') center / cover no-repeat fixed;
}

//====================================================================================================
%noselect {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

//====================================================================================================
ol.counter {
	list-style-type: none;
	counter-reset: item;

	li {
		position: relative;
		padding: 0px 0px 0px 45px;

		&:before {
			counter-increment: item;
			content: counter(item);
			position: absolute;
			left: 0;
			top: 0;
			color: #818181;
			font-size: 14px;
			font-weight: 700;
			text-align: center;
			line-height: 26px;
			border-radius: 50%;
			width: 28px;
			height: 28px;
			border: 1px solid #4274bb;
		}
	}
}

//====================================================================================================
.ellipsis {
	display: block;
	overflow: hidden;
	width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.es {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

//====================================================================================================
.table {
	display: table;
	font-size: 0;
	width: 100%;
	//table-layout: fixed;
}

.trow {
	display: table-row;
}

.cell {
	display: table-cell;

	&.full {
		width: 100%;
	}
}

//====================================================================================================
._ibg {
	position: relative;

	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
}

body.ie {
	._ibg {
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}

	._ibg img {
		width: 0;
		height: 0;
		opacity: 0;
		visibility: hidden;
	}
}

//====================================================================================================
._video {
	position: relative;
	overflow: hidden;
	height: 0;
	padding-bottom: 56.25%;

	video,
	iframe,
	object,
	embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.videobg {

	video,
	iframe,
	object,
	embed {
		position: fixed;
		top: 50%;
		left: 50%;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -100;
		transform: translateX(-50%) translateY(-50%);
		background-size: cover;
	}
}

//====================================================================================================
// ._more-block
._more-block {}

// ._more-content
._more-content {
	transition: all 0.3s ease 0s;
	overflow: hidden;
}

// ._more-item
._more-item {}

// ._more-link
._more-link {
	cursor: pointer;

	span {
		font-style: normal;

		&:first-child {
			display: block;
		}

		&:last-child {
			display: none;
		}
	}

	&._active {
		span {
			font-style: normal;

			&:first-child {
				display: none;
			}

			&:last-child {
				display: block;
			}
		}
	}
}

//====================================================================================================
%graystyle {
	transition: all 0.3s ease 0s;
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}

%graystyleoff {
	filter: grayscale(0);
	-webkit-filter: grayscale(0);
	-moz-filter: grayscale(0);
	-o-filter: grayscale(0);
}

//====================================================================================================

// Swiper-Slider
._swiper {
	overflow: hidden;

	//.swiper-wrapper
	.swiper-wrapper {
		width: 100%;
		height: 100%;
		box-sizing: content-box;
		display: flex;
		position: relative;
	}

	&.swiper-container-vertical {
		.swiper-wrapper {
			flex-direction: column;
		}
	}

	&.swiper-container-autoheight {
		.swiper-wrapper {
			align-items: flex-start;
		}
	}
}

.swiper-container-initialized {
	.swiper-slide {
		flex-shrink: 0;
		// transition-property: transform;
	}
}

.swiper-container-android {

	.swiper-slide,
	.swiper-wrapper {
		transform: translate3d(0px, 0, 0);
	}
}

.swiper-button-lock {
	display: none !important;
}

// Arrows

//====================================================================================================

// Табы
._tabs {}

._tabs-item {
	cursor: pointer;
}

._tabs-block {
	display: none;

	&._active {
		display: block;
	}
}

//=====================================================================================================================
.mirror {
	transform: scale(-1, 1);
}

//=====================================================================================================================
.nicescroll-rails {
	z-index: 1000 !important;
}

//========================================================================================================================================================

// Спойлеры
.spollers-block__title {
	cursor: pointer;
}

//========================================================================================================================================================
._custom-scroll {
	position: fixed;
	right: 0;
	top: 0;
	width: 3px;
	height: 100%;
	z-index: 1000;

	//background-color: rgba(0, 0, 0, 0.1);
	&__line {
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 3px 0 0 3px;
		position: relative;
		left: 0;
		top: 0;
		width: 100%;
	}
}

//========================================================================================================================================================

// Slick-Slider ======================================================================================================================================


//====================================================================================================================================================

// Showmore ==========================================================================================================================================
[data-showmore] {}

[data-showmore-content] {}

[data-showmore-button] {
	span {
		&:last-child {
			display: none;
		}

		._showmore-active & {
			display: none;

			&:last-child {
				display: block;
			}
		}
	}
}

//========================================================================================================================================================

// Стилизация скролла сайта ==============================================================================================================================
::-webkit-scrollbar {
	overflow-y: scroll;
	width: 7px;
	background-color: #f9f9fd;
}

::-webkit-scrollbar-thumb {
	background-color: grey;
}

