.tell-project{
  padding: 60px 0 120px;
  margin-top: 60px;
  @include lit-desktop(){
    padding: 60px 0 100px;
    margin-top: 40px;
  }
  @include tablet(){
    padding: 40px 0 80px;
    margin-top: 40px;
  }
  @include mobile(){
    padding: 40px 0 60px;
    margin-top: 20px;
  }
  &__form{
    display: flex;
    @include tablet(){
      flex-direction: column;
      padding-top: 8px;
    }
    @include mobile(){
      padding-top: 0;
    }
  }
  &__inputs{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    // margin: -12px;
    margin: 0 -8px;
    @include tablet(){
      width: auto;
      flex-wrap: nowrap;
    }
    @include mobile(){
      // margin: -10px;
    }
    @include lit-mobile(){
      // margin: -12px;
      flex-direction: column;
    }
    &-item{
      width: 33.3%;
      padding: 0 8px;
      @include tablet(){
        width: calc(45.4% + 16px);
        flex-shrink: 0;
      }
      @include lit-mobile(){
        width: 100%;
      }
      &--w66{

        width: 66.6%;
        @include tablet(){
          width: 100%;
          flex-shrink: 1;
        }
        @include lit-mobile(){
          margin-top: 16px;
        }
      }
    }
  }

  &__input-container{

    & + &{
      margin-top: 16px;
    }
    &--w100{
      width: 100%;
      height: 100%;
      .tell-project__input{
        height: 100%;
      }
      .input__textarea{
        height: 100%;
        @include lit-mobile(){
          height: 90px;
        }
      }
    }
    // @include mobile(){
    //   padding: 10px;
    // }
    // @include lit-mobile(){
    //   width: 100%;
    //   padding: 8px;
    // }
  }
  &__button-container{
    width: 328px;
    flex-shrink:0;
    margin-left: 36px;
    @include lit-desktop(){
      width: 290px;
      margin-left: 30px;
    }
    @include tablet(){
      margin-top: 30px;
      margin-left: 0;
      width: 100%;
      display: flex;
      align-items: flex-start;
    }
    @include mobile(){
      margin-top: 24px;
    }
    @include lit-mobile(){
      margin-top: 20px;
      flex-direction: column;
    }

  }
  &__button{
    width: 100%;
    margin-bottom: 23px;
    @include tablet(){
      max-width: 293px;
      margin-bottom: 0;
      margin-right: 24px;
    }
    @include mobile(){
      max-width: 45.7%;
      width: 100%;
      flex-shrink: 0;
      margin-right: 20px;
    }
    @include lit-mobile(){
      width: 100%;
      margin: 0 0 12px 0;
      max-width: 100%;
    }
  }
  &__desc{
    padding-right: 15px;
    font-style: italic;

    font-size: 14px;
    line-height: 146%;
    color: #8B868F;
    @include mobile(){
      padding-right: 0;
    }
  }

}
