.burger {
  position: relative;
  height: 20px;
  width: 25px;
  cursor: pointer;
  overflow: hidden;
  z-index: 6000;
}
.burger__icon {
  position: absolute;
  width: 25px;
  height: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.burger__line {
  position: absolute;
  left: 0;
  background-color: #734CA5;
  height: 3px;
  width: 100%;
  border-radius: 3px;
  pointer-events: none;
}
.burger__line--1 {
  top: 0;
}
.burger__line--2 {
  top: 0;
  bottom: 0;
  margin: auto;
  left: auto;
  width: 75%;
  right: 0;
}
.burger__line--3 {
  bottom: 0;
}
.burger.open .burger__line {
  background-color: #FFF;
}
.burger--close5 .burger__line--4, .burger--close5 .burger__line--5 {
  opacity: 0;
  top: 10px;
  transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}
.burger--close5 .burger__line--1 {
  transition: width 0.2s ease-in-out 0.4s;
}
.burger--close5 .burger__line--2 {
  transition: width 0.2s ease-in-out 0.3s;
}
.burger--close5 .burger__line--3 {
  transition: width 0.2s ease-in-out 0.2s;
}
.burger--close5 .burger__line--4 {
  transform: rotate(45deg) translate3d(-25px, 0, 0);
  transition: opacity 0.2s ease-in-out 0.2s, transform 0.2s ease-in-out 0.2s;
}
.burger--close5 .burger__line--5 {
  transform: rotate(-45deg) translate3d(25px, 0, 0);
  transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
}
.burger--close5.open .burger__line--1 {
  width: 0;
  transition: width 0.2s ease-in-out;
}
.burger--close5.open .burger__line--2 {
  width: 0;
  transition: width 0.2s ease-in-out 0.1s;
}
.burger--close5.open .burger__line--3 {
  width: 0;
  transition: width 0.2s ease-in-out 0.2s;
}
.burger--close5.open .burger__line--4 {
  opacity: 1;
  transform: rotate(45deg) translate3d(0, 0, 0);
  transition: opacity 0.2s ease-in-out 0.2s, transform 0.2s ease-in-out 0.2s;
}
.burger--close5.open .burger__line--5 {
  opacity: 1;
  transform: rotate(-45deg) translate3d(0, 0, 0);
  transition: opacity 0.2s ease-in-out 0.4s, transform 0.2s ease-in-out 0.4s;
}

